import React from 'react';
import { Container } from 'reactstrap';
import FontAwesome from 'react-fontawesome';
import Page from '../Components/Page';
import { links } from '../Data';


export default class Contact extends React.Component {
  render() {
    return (
      <Page id="Contact">
        <Container className="text-center">
          <hr/>
          <a className="text-dark" href={links.github} target="_blank">
          <FontAwesome 
            name="github-square"
            size="4x"
          />
          </a>&nbsp;&nbsp;
          <a className="text-dark" href={links.linkedin} target="_blank">
          <FontAwesome 
            name="linkedin-square"
            size="4x"
            />
          </a>&nbsp;&nbsp;
          <a className="text-dark" href={links.gitlab} target="_blank">
          <FontAwesome 
            name="gitlab"
            size="4x"
            />
          </a>
    </Container>
        <br/>
      </Page>
    );
  }
}



