import React from 'react';
import { 
  Container, 
  Row, 
  Col, 
  Card, 
  CardBody,
 } from 'reactstrap';
import ParallaxDivider from '../Components/ParallaxDivider';
import Page from '../Components/Page';
import bg from '../Assets/img/About.jpg';
import me from '../Assets/img/me.jpg';

export default class About extends React.Component {
  render() {
    return (
      <Page id="About">
        <ParallaxDivider 
          bg={bg}
        >
          <Container>
            <h1 className="text-white text-center heading-block">About Me</h1>
          </Container>
        </ParallaxDivider>
        <br/>
        <Container>
          <Card>
            <CardBody>
            <Row>
              <Col md="4">
              <br/>
                <img 
                  src={me}
                  className="img-responsive"
                  alt="Nik Kyriakidis"
                 />
              </Col>
              <Col md="8">
                <div className="text-center">
                    <p>
                      Creative business solutionist with 6 years professional experience. Fast learner ready to hit the ground running and expand on current knowledge base. With a fierce interest in coding, keen to explore creative solutions to business problems. With a genuine passion to make a difference in the world through the magic of code, and constantly striving to revise practices to ensure the latest standards and frameworks to are utilised to achieve this goal. With an ability to excel in both individual and collaborative work I feel confident I will work well in any project that I am assigned to.
                    </p>
                    <h3>Technical Skills</h3>
                    <h4>Programming Languages</h4>
                    <p>PHP - Java - C# - jQuery - Go - HTML - CSS - JavaScript - SASS - LESS</p>
                    <h4>Frameworks</h4>
                    <p>Laravel - Composer - Git - Android - Angular.JS - React - Redux - WebPack - Gulp</p>
                    <br />
                    <blockquote>
                      "Nik is one of the most hard working people I've had the pleasure of working with. He is extremely communicative and always available when we need him, even outside office hours. Nik has constantly gone above and beyond, working diligently to ensure tight deadlines are met. His passion for web developing is evident to anyone who works with him."
                      <br />
                      <span className="text-italic">
                        Harriet Messenger, Sales &amp; Marketing Manager @ Husk Distillers
                      </span>
                    </blockquote>
                </div>
              </Col>
            </Row>
            </CardBody>
          </Card>
        </Container>
      </Page>
    );
  }
}



